<!--  -->
<template>
  <div class="backgroud">
    <Layout style='text-align: center;' :has_return="true" :bottom_nav="bottom_nav" :title="language.examineEnsureTitle" :has_footer="false">
    </Layout>
    <!--  -->
    <span class="lititle">{{language.remark}}</span>
    <div class="remark">
      <textarea v-model="remark" :placeholder="language.textplaceholder"> </textarea>
    </div>
    <!--  -->
    <div class="sure">
      <button @click="ApplicationAuditing">{{language.confirm}}</button>
    </div>
  </div>
</template>
<script type="text/javascript">
import ApiCloud from '@/assets/js/api.min.js'; //apicloudJS
import ChangeLanguage from '../language/changeLanguage.js' //
import Layout from '@/components/Layout';
export default {
  //
  data() {
    return {
      selectRemember: 0,
      language: ChangeLanguage.ChinesesLa,
      title: '', //,
      bottom_nav: [], //
      remark: "", //
      //
      select1: "select",
      select2: "",
      parentMsg: this.$route.query,
    }
  },
  //
  props: {

  },

  //
  computed: {

  },
  //
  wacth: {

  },
  created() {
    this.tellLan();
  },
  //
  mounted() {
    console.log(this.parentMsg.id)
  },
  //
  methods: {
    //
    tellLan() {
      if (this.$store.state.languageType == '') {
        this.language = ChangeLanguage.ChinesesLa;
      } else if (this.$store.state.languageType == 'English') {
        this.language = ChangeLanguage.EnglishLa;
      }
    },

    ApplicationAuditing() {
      this.$messagebox({
        title: '',
        message: '?',
        showCancelButton: true
      });
      this.$messagebox.confirm('?').then(action => {
        if (action == 'confirm') {
          let params = new URLSearchParams();
          params.append('audit', this.parentMsg.audit); //
          params.append('userid', localStorage.getItem("userid")); //id
          params.append('id', this.parentMsg.id); //id
          params.append('remarks', this.remark); //
          this.$store.dispatch('ApplicationAuditing', params).then((response) => {
            let res = response.data;
            if (response.statusText === 'OK' && response.status === 200) {
              //console.log(res);
              if (res.status == "success") {
                this.$toast({
                  message: '',
                  iconClass: 'icon icon-success'
                });
                var s = this.parentMsg.item;
                var num = s.replace(/[^0-9]/ig, "");
                this.$store.commit('updateprocessManageSelect', num);
                this.$router.push({ name: 'ProcessManage' });
              } else {
                this.$toast("");
              }
            } else {
              this.$toast("");
            }
          }).catch((err) => {
            this.$toast("");
          });
        }
      });


      //this.$router.go(-1);
    }

  },
  //
  components: {
    Layout
  }
}

</script>
<style scoped lang="css">
.backgroud {
  background-color: #FAFAFA
}

.lititle {
  display: block;
  margin: 10px 10px;
  font-size: 12px;
  color: #8492A6
}

.selectBox {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-top: 1px solid #E0E6ED;
  border-bottom: 1px solid #E0E6ED;
  margin-top: 10px
}

.selectBox2 {
  padding: 10px 0;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-bottom: 1px solid #E0E6ED;
}

.selectBtnRemark {
  color: red;
  float: right;
  font-size: 14px;
  margin-right: 10px
}


.selectBtn {
  display: inline-block;
  width: 20px;
  height: 20px;
  border: 1px solid #ccc;
  border-radius: 12px;
  margin: 0 10px;
}

.select {
  display: inline-block;
  width: 10px;
  height: 10px;
  border: 6px solid #20A0FF;
  border-radius: 12px;
  margin: 0 10px;
}

.flex {
  display: flex;
}

.remark {
  height: 140px;
  border: 1px solid #E0E6ED;
  padding: 5px 5px;
}

.remark textarea {
  border: none;
  width: 100%;
  height: 100%;
  font-size: 16px
}

.sure {
  text-align: center;
  margin-top: 30px
}

.sure button {
  width: 335px;
  height: 44px;
  background: rgba(32, 160, 255, 1);
  border-radius: 4px;
  font-size: 16px;
  color: #fff;
  border: none
}

</style>



