<!--  -->
<template>
  <div class="background">
    <Layout style='text-align: center;' :has_return="true" :bottom_nav="bottom_nav" :title="language.transferTitle" :has_footer="false">
    </Layout>
    <span class="lititle"><span >*</span>{{language.choosePerson}}</span>
    <div class="transferee" @click="toChooseUser">
      <span>{{language.transferee}}</span>
      <span v-if="!ifHaveUser" class="right">+</span>
      <span class="rightName" v-if="ifHaveUser">{{this.userArray.companyname}}          {{this.userArray.username}}</span>
    </div>
    <!--  -->
    <span class="lititle">{{language.transfereason}}</span>
    <div class="remark">
      <textarea :placeholder="language.fileReaon" v-model="transfeReason"> </textarea>
    </div>
    <!--  -->
    <div class="sure" @click="Confirmation">
      <button>{{language.Confiretransfer}}</button>
    </div>
  </div>
</template>
<script type="text/javascript">
import ApiCloud from '@/assets/js/api.min.js'; //apicloudJS
import ChangeLanguage from '../language/changeLanguage.js' //
import Layout from '@/components/Layout';
export default {
  //
  data() {
    return {
      language: ChangeLanguage.ChinesesLa,
      bottom_nav: [], //
      idTitle: "", //
      ifremarkShow: true, //
      transfeReason: "", //
      //
      ifHaveUser: this.$store.state.transferringMeg.length != 0,
      userArray: this.$store.state.transferringMeg,
      parentMsg: this.$route.query,
    }
  },
  //
  props: {

  },

  //
  computed: {

  },
  //
  wacth: {

  },
  created() {
    this.tellLan();
    console.log(this.$store.state.transferringMeg.length != 0);
  },
  //
  mounted() {

  },
  //
  methods: {
    //
    tellLan() {
      if (this.$store.state.languageType == '') {
        this.language = ChangeLanguage.ChinesesLa;
      } else if (this.$store.state.languageType == 'English') {
        this.language = ChangeLanguage.EnglishLa;
      }
    },
    //
    toChooseUser() {
      this.$router.push({ name: 'ChooseUser', query: this.parentMsg });
    },

    //
    ifHaveUserBack() {
      this.$route.query
    },

    //
    Confirmation() {
      if (!this.ifHaveUser) {
        //this.$toast("");
        this.$toast({
          message: '',
          position: 'bottom',
          duration: 5000
        });
      } else {
        this.$messagebox({
          title: '',
          message: '?',
          showCancelButton: true
        });
        this.$messagebox.confirm('?').then(action => {
          if (action == 'confirm') {
            let params = new URLSearchParams();
            params.append('audit', "change"); //
            params.append('userid', localStorage.getItem("userid")); //id
            params.append('id', this.parentMsg.id); //id
            params.append('transfer', this.userArray.userid); //id
            params.append('remarks', this.transfeReason); //
            this.$store.dispatch('ApplicationAuditing', params).then((response) => {
              let res = response.data;
              if (response.statusText === 'OK' && response.status === 200) {
                if (res.status == "success") {
                  this.$store.commit('updatetransferringMeg', []); //
                  this.$toast({
                    message: '',
                    iconClass: 'icon icon-success'
                  });
                  var s = this.parentMsg.item;
                  var num = s.replace(/[^0-9]/ig, "");
                  this.$store.commit('updateprocessManageSelect', num);
                  this.$router.push({ name: 'ProcessManage' });
                  // var self = this;
                  // setTimeout(function() {
                  //   self.$router.push({ name: 'ProcessManage' });
                  // }, 1500);

                } else {
                  this.$toast("");
                }
              } else {
                this.$toast("");
              }
            }).catch((err) => {
              this.$toast("");
            });

          }
        });



      }

    }


  },
  //
  components: {
    Layout
  }
}

</script>
<style scoped lang="css">
.backgroud {
  background-color: #FAFAFA
}

.lititle {
  display: block;
  margin: 15px 10px;
  font-size: 14px;
  color: #8492A6
}

.lititle span {
  color: red;
  font-size: 14px;
}


.flex {
  display: flex;
}

.transferee {
  border: 1px solid #E0E6ED;
  padding: 10px 10px;
  color: #475669;
  font-size: 14px
}

.transferee .right {
  color: #D3DCE6;
  float: right;
  font-size: 18px
}

.rightName {
  color: #475669;
  float: right;
  font-size: 14px
}



.remark {
  height: 140px;
  border: 1px solid #E0E6ED;
  padding: 5px 5px;
}

.remark textarea {
  border: none;
  width: 100%;
  height: 100%;
  font-size: 16px
}

.sure {
  text-align: center;
  margin-top: 30px
}

.sure button {
  width: 335px;
  height: 44px;
  background: rgba(32, 160, 255, 1);
  border-radius: 4px;
  font-size: 16px;
  color: #fff;
  border: none
}

</style>



