<!--  -->
<template>
  <div class="">
    <Layout style='text-align: center;' :has_return="true" :title="title" :has_footer="false">
      <!-- <h1>{{language}}</h1> -->
    </Layout>
    <mt-button type="primary" @click.native="changLan('Chinese')"></mt-button>
    <mt-button type="primary" @click.native="changLan('English')"></mt-button>
  </div>
</template>
<script type="text/javascript">
import ApiCloud from '@/assets/js/api.min.js'; //apicloudJS
import ChangeLanguage from '../language/changeLanguage.js' //
import Layout from '@/components/Layout';
export default {
  //
  data() {
    return {
      language: ChangeLanguage.ChinesesLa,
      title: '', //

    };
  },
  //
  props: {

  },

  //
  computed: {

  },
  //
  wacth: {

  },
  created() {
    this.tellLan();
  },
  //
  mounted() {

  },
  //
  methods: {
    //
    tellLan() {
      if (this.$store.state.languageType == '') {
        this.language = ChangeLanguage.ChinesesLa;
      } else if (this.$store.state.languageType == 'English') {
        this.language = ChangeLanguage.EnglishLa;
      }
      this.bottom_nav = this.language.bottom_nav_processReport;
    }

  },
  //
  components: {
    Layout
  }
}

</script>
<style scoped lang="css">


</style>



