<template>
  <div>
    <Layout style='text-align: center;' :has_return="false" :title="language.title_userCore" footer_active="userCore" :bottom_nav="bottom_nav">
    </Layout>
    <!-- navbar -->
    <mt-navbar class="page-part" v-model="selected">
      <mt-tab-item id="1"></mt-tab-item>
      <mt-tab-item id="2"></mt-tab-item>
      <mt-tab-item id="3"></mt-tab-item>
    </mt-navbar>
    <div>
      <mt-cell class="page-part" title="">{{ selected }}</mt-cell>
    </div>
    <!-- tabcontainer -->
    <mt-tab-container v-model="selected">
      <mt-tab-container-item id="1">
        <mt-cell :key="n" v-for="n in 10" :title="' ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="2">
        <mt-cell :key="n" v-for="n in 4" :title="' ' + n" />
      </mt-tab-container-item>
      <mt-tab-container-item id="3">
        <mt-cell :key="n" v-for="n in 6" :title="' ' + n" />
      </mt-tab-container-item>
    </mt-tab-container>
  </div>
</template>
<script>
import ChangeLanguage from '../language/changeLanguage.js' //
import Layout from '@/components/Layout';
export default {
  name: 'page-navbar',

  data() {
    return {
      selected: '1',
      language: ChangeLanguage.ChinesesLa,
      bottom_nav: '',
    };
  },

  created() {
    this.tellLan();
  },

  methods: {
    //
    tellLan() {
      if (this.$store.state.languageType == '') {
        this.language = ChangeLanguage.ChinesesLa;
      } else if (this.$store.state.languageType == 'English') {
        this.language = ChangeLanguage.EnglishLa;
      }
    },
  },
  //
  components: {
    Layout
  }
};

</script>



