import Vue from 'vue'
import Router from 'vue-router'
import Login from '@/pages/login'  /**/
import ProcessReport from '@/pages/processReport'  /**/
import ProcessManage from '@/pages/processManage'   /**/
import Hello from '@/pages/Hello'                  /**/
import UserCore from '@/pages/userCore'           /**/
import ApplicationDetailsDMN from '@/pages/applicationDetailsDMN'  /*DMN*/
import ApplicationDetailsRFI from '@/pages/applicationDetailsRFI'  /*RFI*/
import WaitExamine from '@/pages/waitExamine'     /**/
import ExamineEnsure from '@/pages/examineEnsure'  /**/ 
import ExamineRemark from '@/pages/examineRemark'  /**/
import Settings from '@/pages/settings'  /**/
import ApprovalList from '@/pages/approvalList'  /**/ 
import ApprovalTime from '@/pages/approvalTime'  /**/
import Transferring from '@/pages/transferring'  /**/
import ChooseUser from '@/pages/chooseUser'  /**/
import Cropper from '@/pages/cropper'  /**/


Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'Login',
      component: Login  /**/
    },{
      path: '/cc',
      name: 'processReport',
      component: ProcessReport  /**/
    },
    {
      path: '/processReport',
      name: 'ProcessReport',
      component: ProcessReport
    },
    {
      path: '/processManage',
      name: 'ProcessManage',
      component: ProcessManage
    },
    {
      path: '/userCore',
      name: 'UserCore',
      component: UserCore
    },
    {
      path: '/Hello',
      name: 'Hello',
      component: Hello
    },
    {
      path: '/settings',
      name: 'Settings',
      component: Settings
    },
    {
      path: '/applicationDetailsDMN',
      name: 'ApplicationDetailsDMN',
      component: ApplicationDetailsDMN
    },
    {
      path: '/applicationDetailsRFI',
      name: 'ApplicationDetailsRFI',
      component: ApplicationDetailsRFI
    },
    {
      path: '/waitExamine',
      name: 'WaitExamine',
      component: WaitExamine
    },
    {
      path: '/examineEnsure',
      name: 'ExamineEnsure',
      component: ExamineEnsure
    },  {
      path: '/examineRemark',
      name: 'ExamineRemark',
      component: ExamineRemark
    },
    {
      path: '/approvalList',
      name: 'ApprovalList',
      component: ApprovalList
    },
    {
      path: '/approvalTime',
      name: 'ApprovalTime',
      component: ApprovalTime
    },
    {
      path: '/transferring',
      name: 'Transferring',
      component: Transferring
    },
    {
      path: '/chooseUser',
      name: 'ChooseUser',
      component: ChooseUser
    },
    {
      path: '/cropper',
      name: 'Cropper',
      component: Cropper
    }
  ]
})



